.header_01 {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 99999;
    background: transparent;
    // padding: 21px 40px 0 24px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    &.fixedbar {
        position: fixed !important;
        background: #2b8f94 !important;
        // padding: 10px 40px 0 24px;
    }
}

header {
    position: absolute;
    z-index: 9999;
    width: 100%;
    .logo {
        width: 140px;
        padding-left: 10px;
    }
    .navegador {
        background: #f65751;
    }
    .navbar {
        padding-top: 5px;
        padding-bottom: 5px;
        padding-left: 15px;
        padding-right: 30px;
    }
}

.navbar-brand {
    font-weight: 800;
    text-transform: uppercase;
    font-size: 2em;
    color: #262626;
    font-family: "Poppins";
}

.nav-item {
    padding-left: 15px;
    font-weight: 900;
    text-transform: uppercase;
}

.navbar-light .navbar-nav .nav-item .nav-link:hover,
.navbar-light .navbar-nav .nav-item.active .nav-link {
    background: #262626;
    color: #fff;
    letter-spacing: 8px;
    border-radius: 5px;
}

.navbar-light .navbar-nav .nav-item .nav-link {
    color: #262626;
    padding: 8px;
    letter-spacing: 5px;
}

.navbar-light .navbar-toggler {
    border: none;
    color: #fff;
    border: 2px solid #7f7f7f;
    border-radius: 0;
    outline: none;
}

.p_detail {
    font-family: "Poppins" sans-serif;
}

.boton_personalizado {
    margin-left: 40px;
    text-decoration: none;
    padding: 10px;
    font-weight: 600;
    font-size: 20px;
    color: #ffffff;
    background-color: #262626;
    border-radius: 6px;
    &:hover {
        color: #262626;
        background-color: #ffffff;
    }
}

@media (min-width: 375px) and (max-width: 425px) {
    header .navbar-nav {
        padding-left: 29px;
        margin-left: 15px;
    }
    .navbar-nav {
        height: 570px;
        background-image: linear-gradient(120deg, #fbfbfb, coral);
        box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
        border-radius: 20px;
        border-top: 1px solid rgba(255, 255, 255, 0.5);
        border-left: 1px solid rgba(255, 255, 255, 0.5);
        transition: 0.5s;
    }
    .nav-item {
        font-weight: 900;
        padding-top: 31px;
        padding-left: 78px;
        font-size: 20px;
    }
    .navbar-light .navbar-nav .nav-item .nav-link {
        padding-top: 50px;
        &.inicio {
            padding-left: 30px;
        }
        &.Nosotros {
            padding-left: 18px;
        }
        &.contacto {
            padding-left: 18px;
        }
    }
}

@media only screen and (max-width: 768px) {
    header .navbar-nav {
        padding-left: 29px;
        margin-left: 15px;
    }
    .navbar-nav {
        height: 575px;
        background-image: linear-gradient(120deg, #fbfbfb, coral);
        box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
        border-radius: 20px;
        border-top: 1px solid rgba(255, 255, 255, 0.5);
        border-left: 1px solid rgba(255, 255, 255, 0.5);
        transition: 0.5s;
    }
    .nav-item {
        font-weight: 900;
        padding-top: 31px;
        padding-left: 78px;
        font-size: 20px;
    }
    .navbar-light .navbar-nav .nav-item .nav-link {
        padding-top: 50px;
        &.inicio {
            padding-left: 30px;
        }
        &.Nosotros {
            padding-left: 18px;
        }
        &.contacto {
            padding-left: 18px;
        }
    }
}